import { FullscreenRounded } from "@mui/icons-material";
import {
  Chip,
  Grid,
  IconButton,
  Paper,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tooltip,
  Typography,
} from "@mui/material";
import {
  fetchToBePicked,
  fetchLargeShipments,
  fetchPacking,
  fetchCompleted,
} from "common/fetch";
import { DESPDashboard } from "common/models";
import AmberTableRow from "components/Dashboards/AmberTableRow";
import DefaultTableCell from "components/Dashboards/DefaultTableCell";
import FullScreenDespatchTableDialog from "components/Dashboards/FullScreenDespatchTableDialog";
import GreenTableRow from "components/Dashboards/GreenTableRow";
import RedTableRow from "components/Dashboards/RedTableRow";
import { useEffect, useState } from "react";
import { useInterval } from "usehooks-ts";

const tables: string[] = [
  "To Be Picked",
  "Large Shipments",
  "Packing",
  "Completed",
];

export function getLastDespatchColumnName(table: string) {
  switch (table) {
    case "To Be Picked":
      return "Comment";
    case "Completed":
      return "Ready To Collect";
    default:
      return "Status";
  }
}

function DespatchDashboardView() {
  const [toBePicked, setToBePicked] = useState<DESPDashboard[]>();
  const [largeShipments, setLargeShipments] = useState<DESPDashboard[]>();
  const [packing, setPacking] = useState<DESPDashboard[]>();
  const [completed, setCompleted] = useState<DESPDashboard[]>();
  const [fullScreenTable, setFullScreenTable] = useState<string>();
  const [fullScreenDesp, setFullScreenDesp] = useState<DESPDashboard[]>();

  useEffect(() => {
    fetchToBePicked().then((data) => setToBePicked(data));
    fetchLargeShipments().then((data) => setLargeShipments(data));
    fetchPacking().then((data) => setPacking(data));
    fetchCompleted().then((data) => setCompleted(data));
  }, []);

  useInterval(() => {
    fetchToBePicked().then((data) => setToBePicked(data));
    fetchLargeShipments().then((data) => setLargeShipments(data));
    fetchPacking().then((data) => setPacking(data));
    fetchCompleted().then((data) => setCompleted(data));
  }, 2500);

  function handleFullScreenDialogOpen(table: string) {
    setFullScreenTable(table);

    if (table === "To Be Picked") {
      setFullScreenDesp(toBePicked);
    } else if (table === "Large Shipments") {
      setFullScreenDesp(largeShipments);
    } else if (table === "Packing") {
      setFullScreenDesp(packing);
    } else if (table === "Completed") {
      setFullScreenDesp(completed);
    }
  }

  function handleFullScreenDialogClose() {
    setFullScreenTable(undefined);
    setFullScreenDesp(undefined);
  }

  function getRowCount(table: string) {
    switch (table) {
      case "To Be Picked":
        return toBePicked?.length;
      case "Large Shipments":
        return largeShipments?.length;
      case "Packing":
        return packing?.length;
      case "Completed":
        return completed?.length;
      default:
        return 0;
    }
  }

  return (
    <Grid
      container
      rowSpacing={0}
      columnSpacing={0}
      sx={{
        width: "100vw",
        height: "calc(100% - 38px)",
      }}
    >
      {tables.map((table) => {
        return (
          <Grid
            item
            xs={6}
            key={table}
            height="50%"
            sx={{
              borderLeft:
                tables.indexOf(table) === 0 || tables.indexOf(table) === 2
                  ? "0.5px solid rgba(0, 0 , 0, 0.12)"
                  : null,
              borderRight: "0.5px solid rgba(0, 0 , 0, 0.12)",
              borderTop:
                tables.indexOf(table) >= 2
                  ? "0.5px solid rgba(0, 0, 0, 0.12)"
                  : null,
            }}
          >
            <Table size="small" stickyHeader>
              <TableHead>
                <TableRow>
                  <TableCell
                    align="left"
                    colSpan={6}
                    sx={{ borderBottom: "none" }}
                  >
                    <Stack direction="row">
                      <Chip
                        label={getRowCount(table)}
                        variant="outlined"
                        sx={{ mr: 2, fontWeight: "bold" }}
                        size="small"
                      />
                      <Typography fontSize={16} fontWeight="bold">
                        {table}
                      </Typography>
                    </Stack>
                  </TableCell>
                  <TableCell align="right" colSpan={1}>
                    <Tooltip title="Fullscreen" disableInteractive>
                      <IconButton
                        sx={{ py: 0 }}
                        onClick={() => handleFullScreenDialogOpen(table)}
                      >
                        <FullscreenRounded
                          fontSize="medium"
                          htmlColor="black"
                        />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              </TableHead>
            </Table>
            <TableContainer
              component={Paper}
              sx={{
                borderRadius: 0,
                height: "100%",
              }}
            >
              <Table
                size="small"
                stickyHeader
                sx={{
                  borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
                }}
              >
                <TableHead>
                  <TableRow>
                    <DefaultTableCell>Despatch</DefaultTableCell>
                    <DefaultTableCell>Sales Order</DefaultTableCell>
                    <DefaultTableCell>Priority</DefaultTableCell>
                    <DefaultTableCell>Packing Instructions</DefaultTableCell>
                    <DefaultTableCell>Account No.</DefaultTableCell>
                    <DefaultTableCell>Customer</DefaultTableCell>
                    <DefaultTableCell>Ship Date</DefaultTableCell>
                    <DefaultTableCell>
                      {getLastDespatchColumnName(table)}
                    </DefaultTableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {(() => {
                    switch (table) {
                      case "To Be Picked":
                        return (
                          <DespatchTable table={table} items={toBePicked} />
                        );
                      case "Large Shipments":
                        return (
                          <DespatchTable table={table} items={largeShipments} />
                        );
                      case "Packing":
                        return <DespatchTable table={table} items={packing} />;
                      case "Completed":
                        return (
                          <DespatchTable table={table} items={completed} />
                        );
                    }
                  })()}
                </TableBody>
              </Table>
            </TableContainer>
          </Grid>
        );
      })}
      <FullScreenDespatchTableDialog
        handleDialogClose={handleFullScreenDialogClose}
        table={fullScreenTable}
        desp={fullScreenDesp}
      />
    </Grid>
  );
}

export function getLastColumnValue(table: string, item: DESPDashboard) {
  switch (table) {
    case "To Be Picked":
      return item.comment;
    case "Completed":
      return item.readyToCollect ? "Yes" : "No";
    default:
      return item.status;
  }
}

interface DespatchTableProps {
  table: string;
  items: DESPDashboard[] | undefined;
}

function DespatchTable(props: DespatchTableProps) {
  const { table, items } = props;

  return (
    <>
      {items?.map((item) => {
        switch (item.priority) {
          case 1:
            return (
              <RedTableRow key={item.dispatchNote}>
                <DespatchDashboardTableRow table={table} item={item} />
              </RedTableRow>
            );
          case 2:
            return (
              <AmberTableRow key={item.dispatchNote}>
                <DespatchDashboardTableRow table={table} item={item} />
              </AmberTableRow>
            );
          case 3:
            return (
              <GreenTableRow key={item.dispatchNote}>
                <DespatchDashboardTableRow table={table} item={item} />
              </GreenTableRow>
            );
          default:
            return (
              <GreenTableRow key={item.dispatchNote}>
                <DespatchDashboardTableRow table={table} item={item} />
              </GreenTableRow>
            );
        }
      })}
    </>
  );
}

interface DespatchDashboardTableRowProps {
  table: string;
  item: DESPDashboard;
}

export function DespatchDashboardTableRow(
  props: DespatchDashboardTableRowProps
) {
  const { table, item } = props;
  return (
    <>
      <DefaultTableCell component="th" scope="row">
        {item.dispatchNote?.replace("000000000", "")}
      </DefaultTableCell>
      <DefaultTableCell>
        {item.salesOrder?.replace("000000000", "")}
      </DefaultTableCell>
      <DefaultTableCell>{item.priority ? item.priority : ""}</DefaultTableCell>
      <DefaultTableCell>{item.packingInstructions}</DefaultTableCell>
      <DefaultTableCell>{item.accountNumber}</DefaultTableCell>
      <DefaultTableCell>{item.customer}</DefaultTableCell>
      <DefaultTableCell>{item.actualDeliveryDate}</DefaultTableCell>
      <DefaultTableCell>{getLastColumnValue(table, item)}</DefaultTableCell>
    </>
  );
}

export default DespatchDashboardView;
