import {
  Chip,
  Grid,
  Paper,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { ApplicationContext, DataContext } from "common/contexts";
import { fetchPickingOps } from "common/fetch";
import { WipCurrentOp } from "common/models";
import DefaultTableCell from "components/Dashboards/DefaultTableCell";
import DefaultTableRow from "components/Dashboards/DefaultTableRow";
import GreenTableRow from "components/Dashboards/GreenTableRow";
import { useContext, useEffect, useState } from "react";
import { useInterval } from "usehooks-ts";

function PickingDashboardView() {
  const { setCurrentPickingOp } = useContext(DataContext);
  const { setCurrentRoute } = useContext(ApplicationContext);

  const [pickingOps, setPickingOps] = useState<WipCurrentOp[]>();

  useEffect(() => {
    fetchPickingOps().then((data) => setPickingOps(data));
  }, []);

  useInterval(() => {
    fetchPickingOps().then((data) => setPickingOps(data));
  }, 2500);

  return (
    <Grid
      container
      rowSpacing={0}
      columnSpacing={0}
      sx={{
        width: "100vw",
        height: "calc(100% - 54px)",
      }}
    >
      <Grid
        item
        xs={12}
        height="97.5%"
        sx={{
          mx: 5,
          my: 2.5,
        }}
      >
        <Table size="small" stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell
                align="left"
                colSpan={6}
                sx={{
                  borderBottom: "none",
                }}
              >
                <Stack direction="row">
                  <Chip
                    label={pickingOps?.length}
                    variant="outlined"
                    sx={{ mr: 2, fontWeight: "bold" }}
                    size="small"
                  />
                  <Typography fontSize={16} fontWeight="bold">
                    Picking Operations
                  </Typography>
                </Stack>
              </TableCell>
            </TableRow>
          </TableHead>
        </Table>
        <TableContainer
          component={Paper}
          sx={{
            border: "1px solid rgba(0, 0, 0, 0.12)",
            boxShadow: "none",
            height: "100%",
          }}
        >
          <Table
            size="medium"
            stickyHeader
            sx={{
              borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
            }}
          >
            <TableHead>
              <TableRow>
                <DefaultTableCell>Job</DefaultTableCell>
                <DefaultTableCell>Code</DefaultTableCell>
                <DefaultTableCell>Description</DefaultTableCell>
                <DefaultTableCell>Qty</DefaultTableCell>
                <DefaultTableCell>Priority</DefaultTableCell>
                <DefaultTableCell>Purpose</DefaultTableCell>
                <DefaultTableCell>Next Op</DefaultTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {pickingOps?.map((op) => {
                return pickingOps.indexOf(op) === 0 ? (
                  <GreenTableRow
                    key={op.job}
                    onClick={() => {
                      setCurrentPickingOp(op);
                      setCurrentRoute(`/picking/${op.job}`);
                    }}
                  >
                    <DefaultTableCell component="th" scope="row">
                      {op.job?.replace("000000000", "")}
                    </DefaultTableCell>
                    <DefaultTableCell>{op.stockCode}</DefaultTableCell>
                    <DefaultTableCell>{op.stockDescription}</DefaultTableCell>
                    <DefaultTableCell>{op.qtyToMake}</DefaultTableCell>
                    <DefaultTableCell>{op.priority}</DefaultTableCell>
                    <DefaultTableCell>
                      {(op.priority as number) <= 19 ? "Sales" : "Stock"}
                    </DefaultTableCell>
                    <DefaultTableCell>
                      {op.nextWorkCentre === "ASSY01"
                        ? op.nextWorkCentreIMachine
                        : op.nextWorkCentre}
                    </DefaultTableCell>
                  </GreenTableRow>
                ) : (
                  <DefaultTableRow
                    key={op.job}
                    onClick={() => {
                      setCurrentPickingOp(op);
                      setCurrentRoute(`/picking/${op.job}`);
                    }}
                  >
                    <DefaultTableCell component="th" scope="row">
                      {op.job?.replace("000000000", "")}
                    </DefaultTableCell>
                    <DefaultTableCell>{op.stockCode}</DefaultTableCell>
                    <DefaultTableCell>{op.stockDescription}</DefaultTableCell>
                    <DefaultTableCell>{op.qtyToMake}</DefaultTableCell>
                    <DefaultTableCell>{op.priority}</DefaultTableCell>
                    <DefaultTableCell>
                      {(op.priority as number) <= 19 ? "Sales" : "Stock"}
                    </DefaultTableCell>
                    <DefaultTableCell>
                      {op.nextWorkCentre === "ASSY01"
                        ? op.nextWorkCentreIMachine
                        : op.nextWorkCentre}
                    </DefaultTableCell>
                  </DefaultTableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
    </Grid>
  );
}

export default PickingDashboardView;
