import {
  Chip,
  Grid,
  IconButton,
  Paper,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tooltip,
  Typography,
} from "@mui/material";
import { useContext, useEffect, useState } from "react";
import { DashboardTable, WipCurrentOp, WipJobAllLab } from "common/models";
import { useInterval } from "usehooks-ts";
import AllOperationsDialog from "components/Dashboards/AllOperationsDialog";
import { FullscreenRounded, ViewCompactRounded } from "@mui/icons-material";
import FullScreenTableDialog from "components/Dashboards/FullScreenWipTableDialog";
import { ApplicationContext, DataContext } from "common/contexts";
import GreenTableRow from "components/Dashboards/GreenTableRow";
import DefaultTableCell from "components/Dashboards/DefaultTableCell";
import DefaultTableRow from "components/Dashboards/DefaultTableRow";
import {
  fetchWipAssemblyOps,
  fetchTrolleyStorageOps,
  fetchJobAllOps,
} from "common/fetch";

const cells: DashboardTable[] = [
  { name: "CELL01", workCentres: [] },
  { name: "CELL02", workCentres: [] },
  { name: "CELL03", workCentres: [] },
  { name: "CELL04", workCentres: [] },
  { name: "CELL05", workCentres: [] },
  { name: "CELL06", workCentres: [] },
  { name: "CELL07", workCentres: [] },
  { name: "Other", workCentres: [] },
];

function AssemblyDashboardView() {
  const { setCurrentRoute } = useContext(ApplicationContext);
  const { setCurrentAssemblyCell } = useContext(DataContext);

  const [assemblyOps, setAssemblyOps] = useState<WipCurrentOp[]>();
  const [trolleyStorageOps, setTrolleyStorageOps] = useState<WipCurrentOp[]>();
  const [selectedJob, setSelectedJob] = useState<WipCurrentOp>();
  const [selectedJobAllOperations, setSelectedJobAllOperations] =
    useState<WipJobAllLab[]>();
  const [selectedTable, setSelectedTable] = useState<DashboardTable>();
  const [selectedTableOps, setSelectedTableOps] = useState<WipCurrentOp[]>();

  useEffect(() => {
    fetchWipAssemblyOps().then((data) => setAssemblyOps(data));
    fetchTrolleyStorageOps().then((data) => setTrolleyStorageOps(data));
  }, []);

  useInterval(() => {
    fetchWipAssemblyOps().then((data) => setAssemblyOps(data));
    fetchTrolleyStorageOps().then((data) => setTrolleyStorageOps(data));
  }, 2500);

  function handleOnJobSelection(op: WipCurrentOp) {
    fetchJobAllOps(op).then((data) => {
      setSelectedJob(op);
      setSelectedJobAllOperations(data);
    });
  }

  function handleDialogClose() {
    setSelectedJob(undefined);
    setTimeout(() => setSelectedJobAllOperations(undefined), 500);
  }

  function handleSetFullScreenTable(table: DashboardTable) {
    setSelectedTable(table);
    let ops: WipCurrentOp[] = [];
    if (table.name !== "Other") {
      assemblyOps?.forEach((op) => {
        if (table.name === op.workCentre || table.name === op.iMachine)
          ops.push(op);
      });
      trolleyStorageOps?.forEach((op) => {
        if (
          table.name === op.nextWorkCentre ||
          table.name === op.nextWorkCentreIMachine
        ) {
          ops.push(op);
        }
      });
    } else {
      assemblyOps?.forEach((op) => {
        if (
          op.holdFlag === "Y" ||
          otherCells.find((x) => x === op.workCentre || x === op.iMachine)
        ) {
          ops.push(op);
        }
      });
    }
    setSelectedTableOps(ops);
  }

  function handleFullScreenDialogClose() {
    setSelectedTable(undefined);
    setSelectedTableOps(undefined);
  }

  function getRowCount(table: DashboardTable) {
    let counter = 0;
    assemblyOps?.forEach((op) => {
      if (table.name === op.workCentre || table.name === op.iMachine) {
        counter++;
      }
    });
    trolleyStorageOps?.forEach((op) => {
      if (
        table.name === op.nextWorkCentre ||
        table.name === op.nextWorkCentreIMachine
      ) {
        counter++;
      }
    });
    return counter;
  }

  return (
    <Grid
      container
      rowSpacing={0}
      columnSpacing={0}
      sx={{
        width: "100vw",
        height: "calc(100% - 40px)",
      }}
    >
      {cells.map((cell) => {
        return (
          <Grid
            item
            xs={3}
            key={cell.name}
            height="50%"
            sx={{
              borderTop:
                cells.indexOf(cell) >= 4
                  ? "0.5px solid rgba(0, 0, 0, 0.12)"
                  : null,
              borderRight:
                cells.indexOf(cell) === 3 || cells.indexOf(cell) === 7
                  ? null
                  : "0.5px solid rgba(0, 0 , 0, 0.12)",
            }}
          >
            <Table size="small" stickyHeader>
              <TableHead>
                <TableRow>
                  <TableCell
                    align="left"
                    colSpan={6}
                    sx={{ borderBottom: "none" }}
                  >
                    <Stack direction="row">
                      <Chip
                        label={getRowCount(cell)}
                        variant="outlined"
                        sx={{ mr: 2, fontWeight: "bold" }}
                        size="small"
                      />
                      <Typography fontSize={16} fontWeight="bold">
                        {cell.name}
                      </Typography>
                    </Stack>
                  </TableCell>
                  <TableCell align="right" colSpan={1}>
                    <Tooltip title="Select" disableInteractive>
                      <IconButton
                        sx={{ py: 0 }}
                        onClick={() => {
                          setCurrentAssemblyCell(cell.name);
                          setCurrentRoute(`/assembly/${cell.name}`);
                        }}
                      >
                        <ViewCompactRounded
                          fontSize="medium"
                          htmlColor="black"
                        />
                      </IconButton>
                    </Tooltip>
                    <Tooltip title="Fullscreen" disableInteractive>
                      <IconButton
                        sx={{ py: 0 }}
                        onClick={() => handleSetFullScreenTable(cell)}
                      >
                        <FullscreenRounded
                          fontSize="medium"
                          htmlColor="black"
                        />
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                </TableRow>
              </TableHead>
            </Table>
            <TableContainer
              component={Paper}
              sx={{
                borderRadius: 0,
                height: "100%",
              }}
            >
              <Table
                size="small"
                stickyHeader
                sx={{
                  borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
                }}
              >
                <TableHead>
                  <TableRow>
                    <DefaultTableCell>Job</DefaultTableCell>
                    <DefaultTableCell>Code</DefaultTableCell>
                    <DefaultTableCell>Description</DefaultTableCell>
                    <DefaultTableCell>Qty</DefaultTableCell>
                    <DefaultTableCell>Priority</DefaultTableCell>
                    <DefaultTableCell>Purpose</DefaultTableCell>
                    <DefaultTableCell>Next Op</DefaultTableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {cell.name !== "Other" ? (
                    <CellTableBody
                      cell={cell.name}
                      assemblyOps={assemblyOps as WipCurrentOp[]}
                      trolleyStorageOps={trolleyStorageOps as WipCurrentOp[]}
                      handleOnJobSelection={handleOnJobSelection}
                    />
                  ) : (
                    <OtherTableBody
                      assemblyOps={assemblyOps as WipCurrentOp[]}
                      handleOnJobSelection={handleOnJobSelection}
                    />
                  )}
                </TableBody>
              </Table>
            </TableContainer>
          </Grid>
        );
      })}
      <FullScreenTableDialog
        handleDialogClose={handleFullScreenDialogClose}
        table={selectedTable}
        ops={selectedTableOps}
      />
      <AllOperationsDialog
        handleDialogClose={handleDialogClose}
        selectedJob={selectedJob}
        allOperations={selectedJobAllOperations}
      />
    </Grid>
  );
}

interface CellTableBodyProps {
  cell: string;
  assemblyOps: WipCurrentOp[];
  trolleyStorageOps: WipCurrentOp[];
  handleOnJobSelection: (op: WipCurrentOp) => void;
}

function CellTableBody(props: CellTableBodyProps) {
  const { cell, assemblyOps, trolleyStorageOps, handleOnJobSelection } = props;

  return (
    <>
      {assemblyOps?.map((op) => {
        if (cell === op.workCentre || cell === op.iMachine) {
          return (
            <GreenTableRow
              key={op.job}
              onClick={() => handleOnJobSelection(op)}
            >
              <DefaultTableCell component="th" scope="row">
                {op.job?.replace("000000000", "")}
              </DefaultTableCell>
              <DefaultTableCell>{op.stockCode}</DefaultTableCell>
              <DefaultTableCell>{op.stockDescription}</DefaultTableCell>
              <DefaultTableCell>{op.qtyToMake}</DefaultTableCell>
              <DefaultTableCell>{op.priority}</DefaultTableCell>
              <DefaultTableCell>
                {(op.priority as number) <= 19 ? "Sales" : "Stock"}
              </DefaultTableCell>
              <DefaultTableCell>
                {op.nextWorkCentre === "ASSY01"
                  ? op.nextWorkCentreIMachine
                  : op.nextWorkCentre}
              </DefaultTableCell>
            </GreenTableRow>
          );
        } else {
          return null;
        }
      })}
      {trolleyStorageOps?.map((op) => {
        if (cell === op.nextWorkCentre || cell === op.nextWorkCentreIMachine) {
          return (
            <DefaultTableRow
              key={op.job}
              onClick={() => handleOnJobSelection(op)}
            >
              <DefaultTableCell component="th" scope="row">
                {op.job?.replace("000000000", "")}
              </DefaultTableCell>
              <DefaultTableCell>{op.stockCode}</DefaultTableCell>
              <DefaultTableCell>{op.stockDescription}</DefaultTableCell>
              <DefaultTableCell>{op.qtyToMake}</DefaultTableCell>
              <DefaultTableCell>{op.priority}</DefaultTableCell>
              <DefaultTableCell>
                {(op.priority as number) <= 19 ? "Sales" : "Stock"}
              </DefaultTableCell>
              <DefaultTableCell>
                {op.nextWorkCentre === "ASSY01"
                  ? op.nextWorkCentreIMachine
                  : op.nextWorkCentre}
              </DefaultTableCell>
            </DefaultTableRow>
          );
        } else {
          return null;
        }
      })}
    </>
  );
}

const otherCells = ["CELL10", "CELL11", "CELL15"];

interface OtherTableBodyProps {
  assemblyOps: WipCurrentOp[];
  handleOnJobSelection: (op: WipCurrentOp) => void;
}

function OtherTableBody(props: OtherTableBodyProps) {
  const { assemblyOps, handleOnJobSelection } = props;

  return (
    <>
      {assemblyOps?.map((op) => {
        if (
          op.holdFlag === "Y" ||
          otherCells.find((x) => x === op.workCentre || x === op.iMachine)
        ) {
          return (
            <DefaultTableRow
              key={op.job}
              onClick={() => handleOnJobSelection(op)}
            >
              <DefaultTableCell component="th" scope="row">
                {op.job?.replace("000000000", "")}
              </DefaultTableCell>
              <DefaultTableCell>{op.stockCode}</DefaultTableCell>
              <DefaultTableCell>{op.stockDescription}</DefaultTableCell>
              <DefaultTableCell>{op.qtyToMake}</DefaultTableCell>
              <DefaultTableCell>{op.priority}</DefaultTableCell>
              <DefaultTableCell>
                {(op.priority as number) <= 19 ? "Sales" : "Stock"}
              </DefaultTableCell>
              <DefaultTableCell>
                {op.nextWorkCentre === "ASSY01"
                  ? op.nextWorkCentreIMachine
                  : op.nextWorkCentre}
              </DefaultTableCell>
            </DefaultTableRow>
          );
        } else {
          return null;
        }
      })}
    </>
  );
}

export default AssemblyDashboardView;
