import { FullscreenExitRounded } from "@mui/icons-material";
import {
  Dialog,
  TableContainer,
  Paper,
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
  Typography,
  IconButton,
  Tooltip,
  Chip,
  Stack,
} from "@mui/material";
import { DashboardTable, WipCurrentOp } from "common/models";
import {
  getLastColumnName,
  getLastColumnValue,
} from "views/Dashboards/OperationsDashboardView/OperationsDashboardView";
import DefaultTableCell from "components/Dashboards/DefaultTableCell";
import DefaultTableRow from "components/Dashboards/DefaultTableRow";
import GreenTableRow from "components/Dashboards/GreenTableRow";

interface FullScreenWipTableDialogProps {
  handleDialogClose: () => void;
  table: DashboardTable | undefined;
  ops: WipCurrentOp[] | undefined;
}

function FullScreenWipTableDialog(props: FullScreenWipTableDialogProps) {
  const { handleDialogClose, table, ops } = props;

  return (
    <Dialog
      onClose={handleDialogClose}
      open={Boolean(table)}
      maxWidth={false}
      fullWidth
      fullScreen
    >
      <Table size="small" stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell align="left" colSpan={6} sx={{ borderBottom: "none" }}>
              <Stack direction="row">
                <Chip
                  label={ops?.length}
                  variant="outlined"
                  sx={{ mr: 2, fontWeight: "bold" }}
                  size="small"
                />
                <Typography fontSize={16} fontWeight="bold">
                  {table?.name}
                </Typography>
              </Stack>
            </TableCell>
            <TableCell align="right" colSpan={1}>
              <Tooltip title="Exit Fullscreen" disableInteractive>
                <IconButton onClick={handleDialogClose}>
                  <FullscreenExitRounded fontSize="medium" htmlColor="black" />
                </IconButton>
              </Tooltip>
            </TableCell>
          </TableRow>
        </TableHead>
      </Table>
      <TableContainer
        component={Paper}
        sx={{
          borderRadius: 0,
          height: "100%",
        }}
      >
        <Table
          size="medium"
          stickyHeader
          sx={{
            borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
          }}
        >
          <TableHead>
            <TableRow>
              <DefaultTableCell>Job</DefaultTableCell>
              <DefaultTableCell>Code</DefaultTableCell>
              <DefaultTableCell>Description</DefaultTableCell>
              <DefaultTableCell>Qty</DefaultTableCell>
              <DefaultTableCell>Priority</DefaultTableCell>
              <DefaultTableCell>Purpose</DefaultTableCell>
              <DefaultTableCell>
                {getLastColumnName(table?.name as string)}
              </DefaultTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {ops?.map((op) => {
              return table?.name === op.workCentre ||
                table?.name === op.iMachine ? (
                <GreenTableRow key={op.job}>
                  <DefaultTableCell component="th" scope="row">
                    {op.job?.replace("000000000", "")}
                  </DefaultTableCell>
                  <DefaultTableCell>{op.stockCode}</DefaultTableCell>
                  <DefaultTableCell>
                    {table?.name === "Goods Returned for Repair"
                      ? op.jobDescription
                      : op.stockDescription}
                  </DefaultTableCell>
                  <DefaultTableCell>{op.qtyToMake}</DefaultTableCell>
                  <DefaultTableCell>{op.priority}</DefaultTableCell>
                  <DefaultTableCell>
                    {(op.priority as number) <= 19 ? "Sales" : "Stock"}
                  </DefaultTableCell>
                  <DefaultTableCell>
                    {getLastColumnValue(table as DashboardTable, op)}
                  </DefaultTableCell>
                </GreenTableRow>
              ) : (
                <DefaultTableRow key={op.job}>
                  <DefaultTableCell component="th" scope="row">
                    {op.job?.replace("000000000", "")}
                  </DefaultTableCell>
                  <DefaultTableCell>{op.stockCode}</DefaultTableCell>
                  <DefaultTableCell>
                    {table?.name === "Goods Returned for Repair"
                      ? op.jobDescription
                      : op.stockDescription}
                  </DefaultTableCell>
                  <DefaultTableCell>{op.qtyToMake}</DefaultTableCell>
                  <DefaultTableCell>{op.priority}</DefaultTableCell>
                  <DefaultTableCell>
                    {(op.priority as number) <= 19 ? "Sales" : "Stock"}
                  </DefaultTableCell>
                  <DefaultTableCell>
                    {getLastColumnValue(table as DashboardTable, op)}
                  </DefaultTableCell>
                </DefaultTableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </Dialog>
  );
}

export default FullScreenWipTableDialog;
