import { FullscreenExitRounded } from "@mui/icons-material";
import {
  Dialog,
  TableContainer,
  Paper,
  Table,
  TableHead,
  TableRow,
  TableBody,
  TableCell,
  Typography,
  IconButton,
  Tooltip,
  Chip,
  Stack,
} from "@mui/material";
import { DESPDashboard } from "common/models";
import {
  DespatchDashboardTableRow,
  getLastDespatchColumnName,
} from "views/Dashboards/DespatchDashboardView/DespatchDashboardView";
import AmberTableRow from "components/Dashboards/AmberTableRow";
import DefaultTableCell from "components/Dashboards/DefaultTableCell";
import GreenTableRow from "components/Dashboards/GreenTableRow";
import RedTableRow from "components/Dashboards/RedTableRow";

interface FullScreenDespatchTableDialogProps {
  handleDialogClose: () => void;
  table: string | undefined;
  desp: DESPDashboard[] | undefined;
}

function FullScreenDespatchTableDialog(
  props: FullScreenDespatchTableDialogProps
) {
  const { handleDialogClose, table, desp } = props;

  return (
    <Dialog
      onClose={handleDialogClose}
      open={Boolean(table)}
      maxWidth={false}
      fullWidth
      fullScreen
    >
      <Table size="small" stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell align="left" colSpan={6} sx={{ borderBottom: "none" }}>
              <Stack direction="row">
                <Chip
                  label={desp?.length}
                  variant="outlined"
                  sx={{ mr: 2, fontWeight: "bold" }}
                  size="small"
                />
                <Typography fontSize={16} fontWeight="bold">
                  {table}
                </Typography>
              </Stack>
            </TableCell>
            <TableCell align="right" colSpan={1}>
              <Tooltip title="Exit Fullscreen" disableInteractive>
                <IconButton onClick={handleDialogClose}>
                  <FullscreenExitRounded fontSize="medium" htmlColor="black" />
                </IconButton>
              </Tooltip>
            </TableCell>
          </TableRow>
        </TableHead>
      </Table>
      <TableContainer
        component={Paper}
        sx={{
          borderRadius: 0,
          height: "100%",
        }}
      >
        <Table
          size="medium"
          stickyHeader
          sx={{
            borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
          }}
        >
          <TableHead>
            <TableRow>
              <DefaultTableCell>Despatch</DefaultTableCell>
              <DefaultTableCell>Sales Order</DefaultTableCell>
              <DefaultTableCell>Priority</DefaultTableCell>
              <DefaultTableCell>Packing Instructions</DefaultTableCell>
              <DefaultTableCell>Account No.</DefaultTableCell>
              <DefaultTableCell>Customer</DefaultTableCell>
              <DefaultTableCell>Ship Date</DefaultTableCell>
              <DefaultTableCell>
                {getLastDespatchColumnName(table as string)}
              </DefaultTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {desp?.map((item) => {
              switch (item.priority) {
                case 1:
                  return (
                    <RedTableRow key={item.dispatchNote}>
                      <DespatchDashboardTableRow
                        table={table as string}
                        item={item}
                      />
                    </RedTableRow>
                  );
                case 2:
                  return (
                    <AmberTableRow key={item.dispatchNote}>
                      <DespatchDashboardTableRow
                        table={table as string}
                        item={item}
                      />
                    </AmberTableRow>
                  );
                case 3:
                  return (
                    <GreenTableRow key={item.dispatchNote}>
                      <DespatchDashboardTableRow
                        table={table as string}
                        item={item}
                      />
                    </GreenTableRow>
                  );
                default:
                  return (
                    <GreenTableRow key={item.dispatchNote}>
                      <DespatchDashboardTableRow
                        table={table as string}
                        item={item}
                      />
                    </GreenTableRow>
                  );
              }
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </Dialog>
  );
}

export default FullScreenDespatchTableDialog;
