import {
  Card,
  CardMedia,
  Grid,
  Paper,
  Stack,
  Table,
  TableBody,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import { Box } from "@mui/system";
import { DataContext } from "common/contexts";
import {
  fetchAssemblyCellJobs,
  fetchPickList,
  fetchTrolleyStorageJobs,
} from "common/fetch";
import { WipCurrentOp, WipJobPickList } from "common/models";
import DefaultTableCell from "components/Dashboards/DefaultTableCell";
import DefaultTableRow from "components/Dashboards/DefaultTableRow";
import GreenTableRow from "components/Dashboards/GreenTableRow";
import { useContext, useEffect, useState } from "react";

function AssemblyView() {
  const { currentAssemblyCell } = useContext(DataContext);

  const [allJobs, setAllJobs] = useState<WipCurrentOp[]>();
  const [parts, setParts] = useState<WipJobPickList[]>();

  useEffect(() => {
    fetchAssemblyCellJobs(currentAssemblyCell as string).then((data) => {
      fetchTrolleyStorageJobs(currentAssemblyCell as string).then((data1) => {
        if (data && data1) {
          data1.forEach((item) => data.push(item));
          setAllJobs(data);
        }
      });
    });
  }, []);

  useEffect(() => {
    if (allJobs && allJobs?.length > 0) {
      let op = (allJobs as WipCurrentOp[])[0];
      fetchPickList(op).then((data) => setParts(data));
    }
  }, [allJobs]);

  return (
    <Grid
      container
      rowSpacing={0}
      columnSpacing={0}
      sx={{
        minWidth: "100vw",
        height: "calc(100% - 54px)",
      }}
    >
      <Grid
        item
        xs={12}
        height="25%"
        sx={{
          mx: 5,
        }}
      >
        <TableContainer
          component={Paper}
          sx={{
            height: "100%",
            mt: 5,
            border: "1px solid rgba(0, 0, 0, 0.12)",
            boxShadow: "none",
          }}
        >
          <Table
            size="small"
            stickyHeader
            sx={{
              borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
            }}
          >
            <TableHead>
              <TableRow>
                <DefaultTableCell>Job</DefaultTableCell>
                <DefaultTableCell>Code</DefaultTableCell>
                <DefaultTableCell>Description</DefaultTableCell>
                <DefaultTableCell>Qty</DefaultTableCell>
                <DefaultTableCell>Priority</DefaultTableCell>
                <DefaultTableCell>Purpose</DefaultTableCell>
                <DefaultTableCell>Next Op</DefaultTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {allJobs?.map((job) => {
                if (allJobs.indexOf(job) === 0) {
                  return (
                    <GreenTableRow key={job?.job} sx={{}}>
                      <DefaultTableCell component="th" scope="row">
                        {job?.job?.replace("000000000", "")}
                      </DefaultTableCell>
                      <DefaultTableCell>{job?.stockCode}</DefaultTableCell>
                      <DefaultTableCell>
                        {job?.stockDescription}
                      </DefaultTableCell>
                      <DefaultTableCell>{job?.qtyToMake}</DefaultTableCell>
                      <DefaultTableCell>{job?.priority}</DefaultTableCell>
                      <DefaultTableCell>
                        {(job?.priority as number) <= 19 ? "Sales" : "Stock"}
                      </DefaultTableCell>
                      <DefaultTableCell>
                        {job?.nextWorkCentre === "ASSY01"
                          ? job?.nextWorkCentreIMachine
                          : job?.nextWorkCentre}
                      </DefaultTableCell>
                    </GreenTableRow>
                  );
                } else {
                  return (
                    <DefaultTableRow key={job?.job} sx={{}}>
                      <DefaultTableCell component="th" scope="row">
                        {job?.job?.replace("000000000", "")}
                      </DefaultTableCell>
                      <DefaultTableCell>{job?.stockCode}</DefaultTableCell>
                      <DefaultTableCell>
                        {job?.stockDescription}
                      </DefaultTableCell>
                      <DefaultTableCell>{job?.qtyToMake}</DefaultTableCell>
                      <DefaultTableCell>{job?.priority}</DefaultTableCell>
                      <DefaultTableCell>
                        {(job?.priority as number) <= 19 ? "Sales" : "Stock"}
                      </DefaultTableCell>
                      <DefaultTableCell>
                        {job?.nextWorkCentre === "ASSY01"
                          ? job?.nextWorkCentreIMachine
                          : job?.nextWorkCentre}
                      </DefaultTableCell>
                    </DefaultTableRow>
                  );
                }
              })}
            </TableBody>
          </Table>
        </TableContainer>
      </Grid>
      <Grid item xs={12} height="70%" mx={5}>
        <Stack
          direction="row"
          sx={{
            height: "100%",
            mt: 5,
          }}
        >
          <TableContainer
            component={Paper}
            sx={{
              width: "33%",
              mr: 5,
              border: "1px solid rgba(0, 0, 0, 0.12)",
              boxShadow: "none",
            }}
          >
            <Table
              size="small"
              stickyHeader
              sx={{
                borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
              }}
            >
              <TableHead>
                <TableRow>
                  <DefaultTableCell>Code</DefaultTableCell>
                  <DefaultTableCell>Description</DefaultTableCell>
                  <DefaultTableCell>Qty</DefaultTableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {parts?.map((part) => {
                  return (
                    <DefaultTableRow key={part.stockCode}>
                      <DefaultTableCell component="th" scope="row">
                        <strong>{part?.stockCode}</strong>
                      </DefaultTableCell>
                      <DefaultTableCell>
                        {part?.stockDescription}
                      </DefaultTableCell>
                      <DefaultTableCell>{part?.qtyIssued}</DefaultTableCell>
                    </DefaultTableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
          <Card
            sx={{
              width: "100%",
            }}
            variant="outlined"
          >
            <Stack direction="row">
              <Box
                width="40%"
                sx={{
                  m: 2,
                }}
              >
                {allJobs && allJobs.length > 0 && (
                  <CardMedia
                    component="img"
                    image="https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fs3-ap-southeast-2.amazonaws.com%2Fimages.imagescience.com.au%2F_201904_imager-transforms%2Fs3images%2FKnowledge-Base%2FColour-Calibration-and-Lighting%2FPDI-Target%2F5479%2FPDI-Target_8a39ff8a6b9c4355b296f93fc26068cc_4102d750213548c78e74d36e1c2c0494.jpg"
                    alt={allJobs[0].stockDescription}
                  />
                )}
              </Box>
              <Box width="60%"></Box>
            </Stack>
          </Card>
        </Stack>
      </Grid>
    </Grid>
  );
}

export default AssemblyView;
