import { CloseRounded } from "@mui/icons-material";
import {
  Dialog,
  TableContainer,
  Paper,
  Table,
  TableHead,
  TableRow,
  TableBody,
  Typography,
  IconButton,
  TableCell,
  Tooltip,
  Chip,
  Stack,
} from "@mui/material";
import { WipCurrentOp, WipJobAllLab } from "common/models";
import DefaultTableCell from "components/Dashboards/DefaultTableCell";
import DefaultTableRow from "components/Dashboards/DefaultTableRow";
import GreenTableRow from "components/Dashboards/GreenTableRow";

interface AllOperationsDialogProps {
  handleDialogClose: () => void;
  selectedJob: WipCurrentOp | undefined;
  allOperations: WipJobAllLab[] | undefined;
}

function AllOperationsDialog(props: AllOperationsDialogProps) {
  const { handleDialogClose, selectedJob, allOperations } = props;

  return (
    <Dialog
      onClose={handleDialogClose}
      open={Boolean(selectedJob)}
      maxWidth={false}
      fullWidth
      fullScreen
    >
      <Table size="small" stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell align="left" colSpan={6} sx={{ borderBottom: "none" }}>
              <Stack direction="row">
                <Chip
                  label={allOperations?.length}
                  variant="outlined"
                  sx={{ mr: 2, fontWeight: "bold" }}
                  size="small"
                />
                <Typography fontSize={16} fontWeight="bold">
                  All Operations
                </Typography>
              </Stack>
            </TableCell>
            <TableCell align="right" colSpan={1}>
              <Tooltip title="Close" disableInteractive>
                <IconButton onClick={handleDialogClose}>
                  <CloseRounded fontSize="medium" htmlColor="black" />
                </IconButton>
              </Tooltip>
            </TableCell>
          </TableRow>
        </TableHead>
      </Table>
      <TableContainer
        component={Paper}
        sx={{
          borderRadius: 0,
          height: "100%",
        }}
      >
        <Table
          size="medium"
          stickyHeader
          sx={{
            borderBottom: "0.5px solid rgba(0, 0 , 0, 0.12)",
          }}
        >
          <TableHead>
            <TableRow>
              <DefaultTableCell>Job</DefaultTableCell>
              <DefaultTableCell>Operation</DefaultTableCell>
              <DefaultTableCell>Work Centre</DefaultTableCell>
              <DefaultTableCell>Work Centre Description</DefaultTableCell>
              <DefaultTableCell>Planned End Date</DefaultTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allOperations?.map((op) => {
              return selectedJob?.lowestOP === op.operation ? (
                <GreenTableRow key={op.operation}>
                  <DefaultTableCell component="th" scope="row">
                    {op.job?.replace("000000000", "")}
                  </DefaultTableCell>
                  <DefaultTableCell>{op.operation}</DefaultTableCell>
                  <DefaultTableCell>{op.workCentre}</DefaultTableCell>
                  <DefaultTableCell>{op.workCentreDesc}</DefaultTableCell>
                  <DefaultTableCell>
                    {op.plannedEndDate?.toLocaleString("en-GB")}
                  </DefaultTableCell>
                </GreenTableRow>
              ) : (
                <DefaultTableRow key={op.operation}>
                  <DefaultTableCell component="th" scope="row">
                    {op.job?.replace("000000000", "")}
                  </DefaultTableCell>
                  <DefaultTableCell>{op.operation}</DefaultTableCell>
                  <DefaultTableCell>{op.workCentre}</DefaultTableCell>
                  <DefaultTableCell>{op.workCentreDesc}</DefaultTableCell>
                  <DefaultTableCell>
                    {op.plannedEndDate?.toLocaleString("en-GB")}
                  </DefaultTableCell>
                </DefaultTableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </Dialog>
  );
}

export default AllOperationsDialog;
