import {
  AppBar,
  Toolbar,
  ThemeProvider,
  createTheme,
  Box,
} from "@mui/material";
import AssemblyDashboardView from "views/Dashboards/AssemblyDashboardView";
import Logo from "resources/svgs/logo.svg";
import HomeView from "views/HomeView";
import { Route, Routes, useNavigate } from "react-router-dom";
import OperationDashboardView from "views/Dashboards/OperationsDashboardView";
import DespatchDashboardView from "views/Dashboards/DespatchDashboardView";
import PickingDashboardView from "views/Dashboards/PickingDashboardView";
import { ApplicationContext, DataContext } from "common/contexts";
import { useEffect, useState } from "react";
import { WipCurrentOp } from "common/models";
import SalesReportView from "views/Reports/SalesReportView";
import OrdersReportView from "views/Reports/OrdersReportView";
import PlanningReportView from "views/Reports/PlanningReportView";
import OperationsReportView from "views/Reports/OperationsReportView";
import PickingView from "views/PickingView";
import AssemblyView from "../AssemblyView";

function RootView() {
  const navigate = useNavigate();

  const theme = createTheme({
    palette: {
      primary: {
        main: "#e51a0a",
      },
      secondary: {
        main: "#ffffff",
      },
      success: {
        main: "#90ee90",
      },
      error: {
        main: "#ff8a80",
      },
      warning: {
        main: "#ffd54f",
      },
    },
    typography: {
      fontFamily: ["Open Sans", "Roboto", "sans-serif"].join(","),
    },
  });

  const [currentRoute, setCurrentRoute] = useState<string | undefined>("/");
  const [currentAssemblyCell, setCurrentAssemblyCell] = useState<string>();
  const [currentPickingOp, setCurrentPickingOp] = useState<WipCurrentOp>();

  useEffect(() => navigate(currentRoute as string), [currentRoute]);

  function handleNavigateHome() {
    setCurrentRoute("/");
  }

  return (
    <ThemeProvider theme={theme}>
      <ApplicationContext.Provider
        value={{
          currentRoute: currentRoute,
          setCurrentRoute: setCurrentRoute,
        }}
      >
        <AppBar
          position="fixed"
          color="secondary"
          sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }}
          elevation={0}
        >
          <Toolbar variant="dense" sx={{ justifyContent: "space-between" }}>
            <Box
              sx={{
                position: "absolute",
                left: "50%",
                transform: "translate(-50%, 0)",
                ":hover": {
                  cursor: "pointer",
                },
              }}
              onClick={handleNavigateHome}
            >
              <img
                style={{
                  width: "150px",
                }}
                src={Logo}
                alt="logo"
              />
            </Box>
          </Toolbar>
        </AppBar>
        <DataContext.Provider
          value={{
            currentPickingOp: currentPickingOp,
            setCurrentPickingOp: setCurrentPickingOp,
            currentAssemblyCell: currentAssemblyCell,
            setCurrentAssemblyCell: setCurrentAssemblyCell,
          }}
        >
          <Box
            component="main"
            sx={{
              flexGrow: 1,
              height: "calc(100vh - 49px)",
              overflow: "auto",
              marginTop: 6,
              borderTop: "0.5px solid rgba(0, 0, 0, 0.12)",
            }}
          >
            <Routes>
              <Route path="/operations" element={<OperationDashboardView />} />
              <Route path="/assembly" element={<AssemblyDashboardView />} />
              <Route path="/assembly/:cell" element={<AssemblyView />} />
              <Route path="/picking" element={<PickingDashboardView />} />
              <Route path="/picking/:job" element={<PickingView />} />
              <Route path="/despatch" element={<DespatchDashboardView />} />
              <Route
                path="/reports/operations"
                element={<OperationsReportView />}
              />
              <Route path="/reports/sales" element={<SalesReportView />} />
              <Route path="/reports/orders" element={<OrdersReportView />} />
              <Route
                path="/reports/planning"
                element={<PlanningReportView />}
              />
              <Route path="/" element={<HomeView />} />
            </Routes>
          </Box>
        </DataContext.Provider>
      </ApplicationContext.Provider>
    </ThemeProvider>
  );
}

export default RootView;
