interface HomeViewCardProps {
  view: View;
}

function HomeViewCard(props: HomeViewCardProps) {
  const { view } = props;
  const { name, description, icon } = view;
  const { setCurrentRoute } = useContext(ApplicationContext);
  const theme = useTheme();

  return (
    <Grid item xs={6} key={name} padding={4}>
      <Card
        key={name}
        onClick={() => setCurrentRoute(view.route)}
        sx={{
          ":hover": {
            backgroundColor: theme.palette.action.hover,
            cursor: "pointer",
          },
        }}
        variant="outlined"
      >
        <Stack
          direction="row"
          sx={{
            m: 2,
          }}
        >
          <CardContent
            sx={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
            }}
          >
            {icon}
          </CardContent>
          <CardHeader title={name} subheader={description} />
        </Stack>
      </Card>
    </Grid>
  );
}

export default HomeView;
