import { createContext, SetStateAction } from "react";
import { WipCurrentOp } from "common/models";

interface IDataContext {
  currentPickingOp: WipCurrentOp | undefined;
  setCurrentPickingOp: (
    value: SetStateAction<WipCurrentOp | undefined>
  ) => void;
  currentAssemblyCell: string | undefined;
  setCurrentAssemblyCell: (value: SetStateAction<string | undefined>) => void;
}

export const DataContext = createContext<IDataContext>({
  currentPickingOp: undefined,
  setCurrentPickingOp: (value: SetStateAction<WipCurrentOp | undefined>) => {},
  currentAssemblyCell: undefined,
  setCurrentAssemblyCell: (value: SetStateAction<string | undefined>) => {},
});

interface IApplicationContext {
  currentRoute: string | undefined;
  setCurrentRoute: (value: SetStateAction<string | undefined>) => void;
}

export const ApplicationContext = createContext<IApplicationContext>({
  currentRoute: undefined,
  setCurrentRoute: (value: SetStateAction<string | undefined>) => {},
});
